module $ { export class $mol_scroller extends $mol_viewer {

	/// scrollTop 0
	scrollTop() {
		return 0
	}

	/// scrollLeft 0
	scrollLeft() {
		return 0
	}

	/// field * 
	/// 	scrollTop < scrollTop 
	/// 	scrollLeft < scrollLeft
	field( key ) {
		switch( key ){
			case "scrollTop" : return this.scrollTop()
			case "scrollLeft" : return this.scrollLeft()
			default: return super["field"] && super["field"]( key )
		}
	}

	field_keys(){
		return ["scrollTop","scrollLeft"].concat( super["field_keys"] && super["field_keys"]() || [] )
	}

	/// eventScroll null
	@ $mol_prop()
	eventScroll( ...diff ) {
		return ( diff[0] !== void 0 ) ? diff[0] : null
	}

	/// event * 
	/// 	scroll > eventScroll 
	/// 	overflow > eventScroll 
	/// 	underflow > eventScroll 
	/// 	- wheel > eventWheel
	event( key , ...diff ) {
		switch( key ){
			case "scroll" : return this.eventScroll(  ...diff )
			case "overflow" : return this.eventScroll(  ...diff )
			case "underflow" : return this.eventScroll(  ...diff )
			default: return super["event"] && super["event"]( key , ...diff )
		}
	}

	event_keys(){
		return ["scroll","overflow","underflow"].concat( super["event_keys"] && super["event_keys"]() || [] )
	}

} }

